	module.exports = {"data":{"markdownRemark":{"html":"<p>Webサイト開設(1999春)から9年、同人活動開始(2003冬)からは4年半(自分でサークル立ち上げたのが2004冬だから3年半かな)、長いんだか短いんだか、とにかく<em>100万アクセス行きました</em>。もう少しの間活動縮小期間が続くと思いますが、これからもよろしくお願いします。 カウンタはサークル立ち上げと同時に一度リセットしたので、その前も含めるとさらに+25万くらいかな。(こう言うと途端に未練たらしく聞こえてくるから不思議。⑨&#x3C;ふしぎ！)</p>\n<h2>宇都宮から帰ってきたお！</h2>\n<p>ホントは2ヶ月間宇都宮に出張の予定だったのですが、諸事情によりなくなったので帰ってきました。さて色々とTODOが溜まってるので消化しなきゃなぁ……。とか言ってる暇もなく次は和光に転勤。まぁ近いだけマシか。<a href=\"/tag/yb-1\">単車</a>が逝ってなければ直接行けるくらいの近さなんだが……orz</p>\n<h2>スーパー言い訳タイム</h2>\n<p>この絵の言い訳を言っておくッ！ おれは確かにさっきまで魔理沙とあっきゅんを描いていた い…いや…描いていたと言っても その時はまだ下書き段階だったのだが…… 　　　　　　　　 ,. -‐'''''\"\"¨¨¨ヽ 　　　　 　 　 (.＿＿<em>,,,... -ｧァﾌ|　　　　　　　　　　あ…ありのまま 今　起こった事を話すぜ！ 　 　 　 　 　 |i i|　 　 }!　}} /／| 　　　　 　 　 |l､{　 　j}　/,,ｨ//｜　　　　　　　『おれは宮っこ餃子を喰いながら清書していたら 　　　　　　　 i|:!ヾ､</em>ﾉ／ u {:}//ﾍ　　　　　　　　いつのまにかひだまりスケッチになっていた』 　　　　　　　 |ﾘ u' }　 ,ﾉ　<em>,!V,ﾊ | 　　 　 　 ／´fト､</em>{ﾙ{,ィ'ｅﾗ　, ﾀ人　　　　　　　　な…　何を言ってるのか　わからねーと思うが 　　　　 /' 　 ヾ|宀| {´,)⌒<code>/ |&#x3C;ヽﾄiゝ おれも何をされたのかわからなかった ,ﾞ ／ )ヽ iLﾚ u' | | ヾｌﾄﾊ〉 |／_／ ﾊ !ニ⊇ '／:} V:::::ヽ 頭がどうにかなりそうだった… /／ 二二二7'T'' ／u' __ /:::::::/｀ヽ /'´r -―一ｧ‐ﾞＴ´ '\"´ ／::::／-‐ ＼ 宮っこ餃子だから宮子だとか / // 广¨´ /' ／:::::／´￣｀ヽ ⌒ヽ そんなチャチなダジャレじゃ 断じてねえ ﾉ ' / ノ:::::</code>ー-､<em>__／:::::／/ 　 　 　 ヽ　　} _／｀丶　/::::::::::::::::::::::::::￣`ー-{:::...　　　 　　　ｲ　 もっと恐ろしいものの片鱗を味わったぜ… ……と言うわけで[本日のお絵描きは魔理沙と阿求</em>だったもの_](/3660)です。 しかし、それにしても宮子は本当に魔理沙に似ているような気がしてならない。髪と言い性格と言い。そうなるとゆの→あっきゅんだとして、チクリン→毛玉、沙英→香霖、ヒロは……ヒロはアニメ特別編の「関取ごっつあんです」のシーンでエイリアン9を思い出してしまって、もうそれにしか見えない罠。</p>\n<h2>スーパーどうでもいい日記タイム</h2>\n<p><a href=\"http://d.hatena.ne.jp/yaneurao/\">やねうらお氏</a>のコラムぼけっと眺めててVirtualProtect云々のところでふと東方弾幕風のネイティブコンパイラを作れないだろうかとか思ってみた。流石に弾とかその他オブジェクトとかまで全部マシン語にする気力は起きないのでとりあえずネイティブに変換するのはwhileとかloopとかのステートメント部分だけで。(ネイティブコンパイルするとは言え単独のexeが出来るわけでもなく、従来通り弾幕風のようなベースアプリから動作することになる)これだけでも、インタプリタや中間言語よりも大分処理速度が違ってくるような気がしてしまうのはウチだけだろうか？ しかし、最後にアセンブラをカリカリ書いてたのがもう10年以上前で(しかも80286)、今となっては殆ど覚えてない上に、今のマシン語は複雑でおじさんの頭じゃとてもついてこれそうにないどころか、下手すると今のVC++コンパイラはオツム良すぎてインタプリタの方が早いんじゃなかろうか？とか思ってしまうわけであって(いや単にウチが⑨なだけなんですが)、そんな感じで今のところは二の足踏みまくり状態である。丁度良いことに、<a href=\"http://dmf.shrinemaiden.org/OpenDMF\">OpenDMF</a>なる次世代弾幕風が作られつつあるようだし、そっちにｗｋｔｋしつつウチはthG(仮称)を作ってた方がよさそうな気もする。そもそも、よくよく考えたらそのくらいの最適化はやってそうな気がするし、ｍｋｍ氏ならきっと(o ……ってか、今はthG(仮称)だった。あぶねぇまたもや脱線するところだったぜ。 まだ“テスト以前版(体験版以前の以前版)”なので<a href=\"http://thg.danmaq.com/\">まだこっそりよ</a>。</p>","frontmatter":{"cover":null,"date":"2008-04-21T21:43:00+09:00","strDate":"2008/4/21","title":"深謝百萬来場"}}},"pathContext":{"path":"/ja/blog/2008/04/21-深謝百萬来場/","langKey":"ja"}}

